lor:white !important;color:black !important;box-shadow:0px 0px white;-webkit-transform:translateX(5px) translateY(2px);transform:translateX(5px) translateY(2px)}

.header {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
  margin: 9px 1rem;
}

@media (max-width: 1240px) {
  .header {
    flex-direction: column;
    height: 140px;
  }
}
.container{margin-bottom:1.25rem;margin-left:0.5rem;margin-right:calc(0.5rem + 5px);border:solid 5px #181818;padding:1rem;background-color:#444;box-shadow:3px 3px #181818, 5px 5px #EEE}

@-webkit-keyframes fontEffect{0%{background:linear-gradient(#363636, #c7c7c7);-webkit-background-clip:text;background-clip:text}100%{background:linear-gradient(#c9c9c9, #353535);-webkit-background-clip:text;background-clip:text}}@keyframes fontEffect{0%{background:linear-gradient(#363636, #c7c7c7);-webkit-background-clip:text;background-clip:text}100%{background:linear-gradient(#c9c9c9, #353535);-webkit-background-clip:text;background-clip:text}}.logo{-webkit-animation:fontEffect 2s linear infinite;animation:fontEffect 2s linear infinite;color:transparent}

.voteOptions{display:flex;flex-direction:column}

.input{flex:1 1;display:inline-block;vertical-align:middle;-webkit-user-select:none;-ms-user-select:none;user-select:none;padding:0.375rem 0.75rem;font-size:1rem;line-height:1.75;background:white;color:black;font-size:20px;font-weight:400;text-align:left;border:3px solid #000 !important;border-radius:0;padding-right:5%;margin:0px;font-family:"VT323", monospace !important}


.token-item-container{width:100%;margin-top:1rem}.token-item{display:flex;flex-direction:row}.token-item .button{position:relative}.token-component{width:100px;display:flex;flex-direction:row;align-items:center;justify-items:first baseline}.token-component *:first-child{margin-right:8px;width:25px}.selected-item{display:flex;flex-direction:row}.select-popup{position:absolute;z-index:20;left:auto;right:0;top:50px;bottom:auto;width:160px;max-height:50vh;overflow-y:auto;background-color:#fff;cursor:pointer !important}.button.open{z-index:2}.select-popup-item{text-align:left;padding:6px;display:flex;flex-direction:row;align-items:center}

